.navbarOptionWrapper{
    display: block;
    margin-bottom: 5px;
}
.navbarOptionWrapper:last-child{
    margin-bottom: 0;
}
.navbarOptionWrapper a{
    display: block;
    text-decoration: none;
    padding: 10px;
    color: white;
    border-radius: 25px;
}
.navbarOptionWrapper a:hover, a.active{
    background: #4285F4;
}
.navbarOptionWrapper a.active{
    color: lightgrey;
}