.dialogItemWrapper {
    color: white;
    grid-area: dialog;
    margin-bottom: 5px;
}
.dialogItemWrapper:last-child {
    margin-bottom: 0;
}
.dialogItemWrapper a{
    display: block;
    text-decoration: none;
    padding: 10px;
    color: white;
    border-radius: 25px;
}
.dialogItemWrapper a:hover, a.active{
    background: #4285F4;
}
.dialogItemWrapper a.active{
    color: #F4B400;
}