.textarea {
    display: block;
    resize: none;
    padding: 10px;
    border: none;
    background: none;
    color: #FFFFFF;
    font-size: 18px;
    position: relative;
    border-bottom: 1px solid lightgrey;
    min-height: 25px;

    margin-bottom: 10px;
}
.textarea::placeholder{
    font-size: 18px;
    color: lightgrey;
}
.textarea:focus{
    outline: none;
}
.button {
    font-size: 12px;
    padding: 5px;
}
.error {
    border: 2px green solid;
}