* {
    color: white;
}
.container{
    border-right: 1px solid rgb(47, 51, 54);
    border-left: 1px solid rgb(47, 51, 54);

    width: 600px;
    height: 1000px;
}
.wrapper{
    display: block;
    max-width: 935px;

}
.profileHeader{
    position: relative;
    height: 0;
    padding-top: 33.3%;
}
.profileHeaderImage{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
}
.profileInfoContainer{
    padding: 12px 16px 0 16px;
}
.topInfoPanel{
    display: flex;
    justify-content: space-between;
}
.profilePictureContainer{
    margin-top: -100%;
    width: 22.5%;

}
.profilePicture{
    border-radius: 100%;
    width: 100%;
    border: 4px solid black;
}












.headerWrapper{
    display: flex;
    height: 200px;
}
.interactiveData{
    flex-grow: 4.34;
    background: black
}
.fullName{
    flex: 1 1 0;
    color: white;
    font-size: 20px;
}
.headerButtons{
    flex: 2 1 0;
}
.headerMain{;
    display: flex;
}
