.dialogsWrapper {
    display: grid;
    grid-template-areas:
    "dialog messages";
    grid-template-columns: 2fr 10fr;
}
.dialogsElements, .messagesElements{
    padding: 10px;
    border-right: 1px solid #7f7f7f;
}
.dialogsElements a:hover, a.active{
    background: #4285F4;
}
.dialogsElements a.active{
    color: #F4B400;
}