.wrapper{
    margin-bottom: 20px;
    display: flex;
    align-items: start;
}
.profilePictureContainer{
    width: 10%;
    height: auto;
    aspect-ratio: 1/1;
}
.profilePictureWrapper{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}
.profilePictureWrapper > a{
    height: 80%;
}
.profilePicture{
    height: 100%;
}
.followButtonContainer{
    display: flex;
    justify-content: center;
}
.info {
    width: 100%;
    padding: 20px;
}